import * as Yup from "yup";
import PropTypes from "prop-types";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import React, { useState, useEffect } from "react";
// @mui
import {
  Box,
  Card,
  Stack,
  Button,
  Collapse,
  Typography,
  Alert,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import cardValidator from "card-validator";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
} from "../../../../components/hook-form";
import Iconify from "../../../../components/Iconify";
import useIsMountedRef from "../../../../hooks/useIsMountedRef";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../../../redux/store";
import {
  postRecharge,
  startLoading,
  stopLoading,
} from "../../../../redux/slices/wallet";
import useAuth from "../../../../hooks/useAuth";
// ----------------------------------------------------------------------

Recharge.propTypes = {
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
};

export default function Recharge({ isOpen, onCancel }) {
  const dispatch = useDispatch();
  const { user, refresh } = useAuth();
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  const { isLoading } = useSelector((state) => state.wallet);

  const UpdateUserSchema = Yup.object().shape({
    type_depo: Yup.string().required("Type account is required"),
    amount: Yup.number().min(10).required("Amount is required"),
  });

  const defaultValues = {
    type_depo: "USD",
    amount: 5,
  };

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    watch,
    setError,
    reset,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const values = watch();

  const onSubmit = async (data) => {
    try {
      dispatch(startLoading());
      const res = await postRecharge(data);
      if (res?.redirect_url) {
        dispatch(stopLoading());
        setTimeout(() => {
          window.location.href = res.redirect_url;
        }, 100);
        return;
      }
      dispatch(stopLoading());
    } catch (error) {
      enqueueSnackbar(error?.response?.data?.detail, { variant: "error" });
      dispatch(stopLoading());
      if (isMountedRef.current) {
        if (isMountedRef.current) {
          setError("afterSubmit", {
            ...error,
            message: error.response.data?.detail,
          });
        }
      }
    }
  };

  return (
    <Collapse in={isOpen}>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Card sx={{ p: 3 }}>
          <Typography
            variant="overline"
            sx={{ mb: 3, display: "block", color: "text.secondary" }}
          >
            {t("params.deposit_account")}
          </Typography>
          {!!errors.afterSubmit && (
            <Alert severity="error">{errors.afterSubmit.message}</Alert>
          )}
          <Box
            sx={{
              padding: 3,
              borderRadius: 1,
            }}
          >
            <Stack spacing={3}>
              <Stack spacing={2} direction={{ xs: "column", md: "row" }}>
                <RHFSelect
                  name="type_depo"
                  label={t("params.wallet")}
                  placeholder={t("params.wallet")}
                >
                  <option value="" />
                  {[
                    { label: "Gourde", value: "HTG" },
                    { label: "Dollar", value: "USD" },
                  ].map((option, index) => (
                    <option key={index} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </RHFSelect>

                <RHFTextField
                  name="amount"
                  label={`Montant ${values?.type_depo}`}
                  placeholder="Montant"
                  type="number"
                />
              </Stack>

              <Stack direction="row" justifyContent="flex-end" spacing={1.5}>
                <Button color="inherit" variant="outlined" onClick={onCancel}>
                  {t("params.cancel")}
                </Button>
                <LoadingButton
                  loading={isLoading}
                  type="submit"
                  variant="contained"
                >
                  {t("params.recharge")}
                </LoadingButton>
              </Stack>
            </Stack>
          </Box>
        </Card>
      </FormProvider>
    </Collapse>
  );
}
